/* ---------------------------- */
/* ---------- ROOT CSS -------- */
/* ---------------------------- */
@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;600;700&display=swap");
/* https://piccalil.li/blog/a-modern-css-reset/ */
/* ---------------------------- */
/* --------- RESET CSS -------- */
/* ---------------------------- */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list],
ul[class] {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  /* colors */
  --clr-primary: hsl(223, 87%, 63%);
  --clr-secondary-blue: hsl(223, 100%, 88%);
  --clr-secondary-red: hsl(354, 100%, 66%);
  --clr-neutral-gray: hsl(0, 0%, 59%);
  --clr-neutral-dark-blue: hsl(209, 33%, 12%);
  /* font stuff */
  --ff-primary: "Libre Franklin", sans-serif;
}

/* ---------------------------- */
/* ---------- UTILS ----------- */
/* ---------------------------- */
.container {
  width: min(85%, 40rem);
  margin: 0 auto;
}

/* ---------------------------- */
/* ---------- GLOBALS --------- */
/* ---------------------------- */
/* general */
html, body {
  width: 100%;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  font-family: var(--ff-primary);
  font-size: clamp(0.75rem, 1vw + 0.25rem, 1rem);
}

main {
  flex-grow: 1;
}

header, main, footer {
  flex-shrink: 0;
}

/* typography */
h1 {
  font-size: clamp(1.375rem, 3vw + 0.5rem, 3rem);
  font-weight: 300;
  color: var(--clr-neutral-gray);
}

strong {
  font-weight: 700;
  color: var(--clr-neutral-dark-blue);
}

/* button styles */
.btn, .btn-primary {
  display: inline-block;
  padding: 1.15em 3.875em;
  border: none;
  border-radius: 1.75rem;
  font-size: clamp(0.75rem, 1vw + 0.25rem, 1rem);
  font-weight: 600;
  line-height: 1.35;
}

.btn-primary {
  background-color: var(--clr-primary);
  color: white;
  box-shadow: 0px 5px 10px 2px rgba(76, 123, 243, 0.230414);
}
.btn-primary:hover, .btn-primary:focus {
  opacity: 0.8;
}

/* form styles */
input[type=email] {
  background: none;
  border: 1px solid var(--clr-secondary-blue);
  border-radius: 1.75rem;
  padding: 1em 2rem;
  color: #859fe0;
}
input[type=email]::placeholder {
  font-size: clamp(0.75rem, 1vw + 0.25rem, 1rem);
}
input[type=email]:focus-visible {
  border: 1px solid var(--clr-primary);
  outline: none;
}

.invalid,
.invalid:focus,
.invalid:focus-visible {
  outline: 1px solid var(--clr-secondary-red);
  border: 1px solid var(--clr-secondary-red);
}

.error-message {
  padding-top: 1em;
  font-size: clamp(0.625rem, 1vw + 0.25rem, 0.75rem);
  font-style: italic;
  color: var(--clr-secondary-red);
}

.header {
  margin-top: 5rem;
  margin-bottom: 2rem;
}
.header img {
  margin: 0 auto;
  width: clamp(3.5rem, 5vw + 1.5rem, 5.625rem);
}

.launching {
  text-align: center;
}
.launching__title {
  padding-bottom: 0.5em;
}
.launching__text {
  color: var(--clr-neutral-dark-blue);
  margin-bottom: 1.75rem;
}
.launching__form {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 1fr;
  grid-auto-flow: dense;
  gap: 0.5rem;
  max-width: 90%;
  margin: 0 auto;
}
@media (min-width: 48rem) {
  .launching__form {
    flex-direction: row;
    grid-template-columns: 2fr 12.5rem;
    grid-template-rows: 1fr 1.5rem;
  }
  .launching__form .form-group {
    flex-grow: 1;
    text-align: start;
    grid-row: 1/3;
    align-self: start;
  }
  .launching__form .form-group .error-message {
    padding-left: 2rem;
  }
  .launching__form button {
    flex-grow: 0;
    justify-self: flex-end;
    grid-row: 1/2;
  }
}
.launching__form .form-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.launching__image {
  margin-top: 5rem;
}
@media (min-width: 48rem) {
  .launching__image {
    margin-top: 3.5rem;
  }
}
@media (min-width: 75rem) {
  .launching__image {
    margin-top: 2rem;
  }
}

.footer .copyright {
  margin-top: 1.75rem;
  margin-bottom: 2.5rem;
  text-align: center;
  font-size: clamp(0.625rem, 1vw + 0.25rem, 0.75rem);
  color: var(--clr-neutral-gray);
}

.social-links__list {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.social-links__link {
  text-decoration: none;
  color: var(--clr-primary);
}
.social-links .icon {
  width: 2rem;
  height: 2rem;
  border: 1px solid var(--clr-secondary-blue);
  border-radius: 50%;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social-links .icon:hover, .social-links .icon:focus {
  border: 1px solid var(--clr-primary);
  background-color: var(--clr-primary);
  color: white;
}
.social-links .sr-only {
  visibility: hidden;
}